<template>
  <aside class="filter">
    <h2 class="filter__title"></h2>
    <form class="filter__form form" action="#" method="get" @submit.prevent="submit">
      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price"
                 v-model.number="currentPriceFrom">
          <span class="form__value"></span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price"
                 v-model.number="currentPriceTo">
          <span class="form__value"></span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category"
                  v-model.number="currentCategoryId">
            <option value="0"> </option>
            <option v-for="category in categories"
                    :key="category.id" :value="category.id">{{ category.title }}
            </option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend"></legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" name="color"
                     :value="color.id" v-model="currentColorId">
              <span class="colors__value" :style="'background-color:'+color.code"></span>
            </label>
          </li>
        </ul>
      </fieldset>
      <button class="filter__submit button button--primery" type="submit"></button>
      <button class="filter__reset button button--second" type="button" @click="reset"></button>
    </form>
  </aside>
</template>

<script>

import { API_BASE_URL } from '@/config';
import axios from 'axios';

export default {
  name: 'ProductFilter',
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorId'],
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColorId: 0,
      categoriesData: null,
      colorsData: null
    };
  },
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    colors() {
      return this.colorsData ? this.colorsData.items : [];
    },
  },
  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategoryId = value;
    },
    colorId(value) {
      this.currentColorId = value;
    },
  },
  methods: {
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategoryId);
      this.$emit('update:colorId', this.currentColorId);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      this.$emit('update:colorId', 0);
    },
    loadCategories() {
      axios.get(API_BASE_URL + `/api/productCategories`)
        .then(response => this.categoriesData = response.data);
    },
    loadColors() {
      axios.get(API_BASE_URL + `/api/colors`)
        .then(response => this.colorsData = response.data);
    }

  },
  created() {
    this.loadCategories();
    this.loadColors();
  }
};
</script>
