      <div class="l_main">
        <div class="l_square"><span></span><span></span><span></span></div>
        <div class="l_square"><span></span><span></span><span></span></div>
        <div class="l_square"><span></span><span></span><span></span></div>
        <div class="l_square"><span></span><span></span><span></span></div>
      </div>
    </div>
  </main>
  <main class="content container" v-else-if="!productData">
    <div>    
      <button @click.prevent="loadProduct">  </button>
    </div>
  </main>
  <main class="content container" v-else="productData">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}"></router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name:'main'}">{{ category.title }}</router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">{{ product.title }}</a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img :src="imageUrl" :alt="product.title">
        </div>
      </div>
      <div class="item__info">
        <span class="item__code">: {{ product.id }}</span>
        <h2 class="item__title">{{ product.title }}</h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">{{ product.price | numberFormat }} </b>
            <fieldset class="form__block">
              <legend class="form__legend">:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="color in product.colors" :key="color.id">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" value="#73B6EA">
                    <span class="colors__value" :style="`background-color: ${color.code};`"></span>
                  </label>
                </li>
              </ul>
            </fieldset>
            <FormCurrent @counter-change="onAmountCountChange" :dis=productAddSending />
            <div v-show="productAdded">   </div>
            <div v-show="productAddSending">   </div>
          </form>
        </div>
      </div>
      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current"></a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"></a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#"></a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">  </a>
          </li>
        </ul>
        <div class="item__content">
          <p>
             GPS, , BEIDOU Galileo  QZSS<br>
              <br>
              Bluetooth Smart, ANT+  Wi-Fi<br>
              <br>
          </p>
          <a href="#"> </a>
          <h3> ?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS   ,     ,  
             . Wahoo ELEMNT BOLT GPS    ANT+,   
             .    ,     .    
                 ,      .
               ,      .  
                   .
          </p>
          <h3></h3>
          <p>
             Wahoo ELEMNT BOLT  .     74,6 x 47,3 x 22,1 .
                .      .  
                 56 .      , 
                  : ,  
            ,   .. (     ).      
             IPX7. ,     ,     ( 30 )
                   1.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import FormCurrent from '@/components/FormCurrent';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { mapActions } from 'vuex';

export default {
  name: 'ProductPage',
  components: { FormCurrent },
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false
    };
  },
  filters: {
    numberFormat
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
    imageUrl() {
      return this.productData.image.file.url;
    },

  },
  methods: {
    ...mapActions(['addProductCart']),
    onAmountCountChange: function (amount) {
      this.productAmount = amount;
    },
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductCart({
        productId: this.product.id,
        amount: this.productAmount
      })
        .then(() => {
          this.productAdded = true;
          this.productAddSending = false;
        });
    },
    loadProduct() {
      this.productLoadingFailed = false;
      this.productLoading = true;
      clearTimeout(this.loadProductTimer);
      this.loadProductTimer = setTimeout(() => {
        axios.get(API_BASE_URL + `/api/products/` + this.$route.params.id)
          .then(response => this.productData = response.data)
          .catch(() => this.productLoadingFailed = true)
          .then(() => this.productLoading = false);
      }, 1000);
    },
  },
  watch: {
    '$route.params.id': {
      handler() {
        this.loadProduct();
      },
      immediate: true
    }
  }
};
</script>
