<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" :alt="item.product.title">
    </div>
    <h3 class="product__title">{{ item.product.title }}</h3>
    <span class="product__code">: {{ item.productId }}</span>
    <div class="product__counter form__counter">
      <button type="button" aria-label="  ">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>
      <input type="text" v-model.number="amount" name="count">
      <button type="button" aria-label="  ">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>
    <b class="product__price">{{ item.product.price * item.amount | numberFormat }} </b>
    <button class="product__del button-del" type="button" aria-label="   "
            @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>

</template>


<script>
import numberFormat from '@/helpers/numberFormat';
import { mapActions } from 'vuex';

export default {
  name: 'CartItem',
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', {
          productId: this.item.productId,
          amount: value
        });
      }
    }
  },
  filters: {
    numberFormat
  },
  methods: {
    ...mapActions(['deleteProductFromCart']),

    deleteProduct(productId) {
      this.deleteProductFromCart({ productId });
    }
  }
};

</script>
